<template>
  <div class="home-container">
    <!-- NavBar区域 -->
    <van-nav-bar title="黑马头条" fixed />
    <!-- 导入ArticleInfo组件 -->
    <van-pull-refresh v-model="isloading" :disabled="finished" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <ArticleInfo v-for="item in articlelist" :key="item.id" :title="item.title" :author="item.aut_name"
          :count="item.comm_count" :update="item.pubdate" :cover="item.cover">
        </ArticleInfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArcInfoAPI } from '@/APi/arcInfoApi'
import ArticleInfo from '@/components/article/ArticleInfo.vue'
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Home',
  components: {
    ArticleInfo
  },
  data() {
    return {
      page: 1,
      limit: 10,
      articlelist: [],
      // 是否正在加载数据，如果loading为true，则不会触发load事件
      loading: true,
      // 所有数据是否加载完毕，如果没有更多数据改为true
      finished: false,
      isloading: true
    }
  },
  created() {
    this.initArticleList()
    this.loading = true
  },
  methods: {
    // 封装获取文章列表的方法
    async initArticleList(isRefresh) {
      const { data: res } = await getArcInfoAPI(this.page, this.limit)
      if (isRefresh) {
        this.articlelist = [...res, ...this.articlelist]
        this.isloading = false
      } else {
        this.articlelist = [...this.articlelist, ...res]
        this.loading = false
      }
      if (res.length === 0) {
        // 数据加载完了
        this.finished = true
      }
    },
    onLoad() {
      console.log('上拉加载')
      this.page++
      // 调用接口方法
      this.initArticleList()
    },
    onRefresh() {
      console.log('下拉刷新')
      this.page++
      this.initArticleList(true)
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  padding: 46px 0 50px 0;
}

.van-nav-bar {
  background-color: #007bff;
}

/deep/.van-nav-bar__title {
  color: #fff;
}
</style>
